<script>
// @ is an alias to /src
import { Vue2InteractDraggable } from "vue2-interact";
import debounce from "debounce";

export default {
  name: "Home",
  components: {
    Vue2InteractDraggable
  },
  created() {
    window.addEventListener("resize", this.myEventHandler);
  },
  mounted() {
    this.resetCard();
    this.resetThis++;
    this.updateStack();
    this.coordinates = this.$refs.cardStack.getBoundingClientRect();
  },
  destroyed() {
    window.removeEventListener("resize", this.myEventHandler);
  },
  data: () => {
    return {
      coordinates: { top: 0 },
      resetThis: 1,
      title: "",
      text: "",
      nextTitle: "",
      nextText: "",
      tempLipsum: [
        {
          title: "सुन न मा स्वागत छ",
          text:
            "सुन न नया नेपाली एप हो । यो एप प्रयोग गरी तिमी आफ्ना कुराहरु अरुलाई सुनाउन सक्छौ, र अरुका कुराहरु सुन्न सक्छौ । ",
          bg: "clip1.png"
        },
        {
          title: "सुन न कसरी चलाउने",
          text:
            "यो स्क्रीनमा अन्जान मान्छेहरुले सुनाएका करुाहरु पढ । केही भन्न मन लागे तलको मेसेज पठाउने भाँडोमा लेखेर पठाउ । हामी सुरक्षित लेख्ने मान्छेसम्म पुर्याउने छऔँ । देब्रे स्क्रीनमा आफ्ना कुराहरु लेख । दाहिने स्क्रीनमा पठाएको तथा आएका मेसेजहरु पढ ।"
        },
        {
          bg: "welcome.png",
          title: "ल bro अब चिलसँग सुन न चलाउ",
          text: "सबै साथी हुन्, त्यसैले भेदभाव, गालीहरु अनी अरु कुराहरु नगर ल।"
        }
      ],
      tempCount: 0,
      hasBg: false,
      hasBgNext: false,
      bg: "",
      bgNext: ""
    };
  },
  methods: {
    messageFocused() {
      document.body.classList.add("enableAndroidKeyboard");
    },
    messageBlurred() {
      document.body.classList.remove("enableAndroidKeyboard");
      window.scrollTo(0, 0);
    },
    sendMessage() {
      alert("Sending Message :p");
    },
    myEventHandler() {
      if (document.activeElement === this.$refs.message) {
        this.$refs.mainCard.classList.add("floating");
      }
    },
    moving(e) {
      debounce(
        e => {
          let percent = (e.x / window.innerWidth) * 100;
          percent = Math.sign(percent) * Math.pow(1.137, Math.abs(percent));
          if (percent > 90) percent = 90;
          else if (percent < -90) percent = -90;
          if (e.x > 0) {
            this.$refs.overlayAccept.style.opacity = percent + "%";
            this.$refs.overlayAccept.style.display = "block";
            this.$refs.overlayReject.style.display = "none";
          } else if (e.x < 0) {
            this.$refs.overlayReject.style.opacity = Math.abs(percent) + "%";
            this.$refs.overlayReject.style.display = "block";
            this.$refs.overlayAccept.style.display = "none";
          } else {
            this.$refs.overlayAccept.style.opacity = "0%";
            this.$refs.overlayReject.style.opacity = "0%";
            this.$refs.overlayAccept.style.display = "none";
            this.$refs.overlayReject.style.display = "none";
          }
        },
        100,
        true
      )(e);
    },
    resetMoving() {
      this.$refs.overlayAccept.style.opacity = "0%";
      this.$refs.overlayReject.style.opacity = "0%";
      this.$refs.overlayAccept.style.display = "none";
      this.$refs.overlayReject.style.display = "none";
    },
    draggedRight() {
      this.resetCard();
      console.log("right");
    },
    draggedLeft() {
      this.resetCard();
      console.log("left");
    },
    resetCard() {
      setTimeout(() => {
        this.resetThis++;
        this.$nextTick(() => {
          this.$refs.cardStack.classList.add("animateStackUp");
        });
        this.updateStack();
      }, 200);
    },
    updateStack() {
      this.title = this.tempLipsum[this.tempCount].title;
      this.text = this.tempLipsum[this.tempCount].text;
      if (this.tempLipsum[this.tempCount].bg) {
        this.hasBg = true;
        this.bg = this.tempLipsum[this.tempCount].bg;
      } else this.hasBg = false;

      this.tempCount = (this.tempCount + 1) % this.tempLipsum.length;
      this.nextTitle = this.tempLipsum[this.tempCount].title;
      this.nextText = this.tempLipsum[this.tempCount].text;
      if (this.tempLipsum[this.tempCount].bg) {
        this.hasBgNext = true;
        this.bgNext = this.tempLipsum[this.tempCount].bg;
      } else this.hasBgNext = false;
    },
    xyOfCard() {
      return this.$refs.cardStack.getBoundingClientRect().top;
    }
  },
  computed: {
    yOfCard() {
      console.log(this.$refs.cardStack);
      return this.$refs.cardStack.getBoundingClientRect().top;
    }
  },
  watch: {
    xyOfCard: () => {
      console.log("fuck");
    }
  }
};
</script>

